<template>
    <v-container style="width: 100%; " align="center">
        <v-app-bar
                fixed
                app
                color="#FFFFFF"
                elevation="3"
        >
            <v-row>
                <v-col></v-col>
                <v-col class="text-center">
                    <v-toolbar-title>{{usersText}}</v-toolbar-title>
                </v-col>
                <v-col class="text-right" style="padding-top: 0; padding-bottom: 0">
                    <v-btn class="app-bar-button" small tile style="display: inline;" text color="primary"
                           @click="openSearchFilter">{{toFilterText}}
                    </v-btn>
                </v-col>
            </v-row>


        </v-app-bar>

        <div style="margin-top: 64px"/>

        <v-row class="text-center" style="height: 58px; padding-bottom: 0px">
            <v-col style="padding-right: 0px; padding-top: 4px">
                <v-text-field
                        v-model="searchByUsernameField"
                        :rules="[rules.min5Char]"
                        :label="searchLabel"
                        solo
                        dense
                        clearable
                        rounded
                ></v-text-field>
            </v-col>
            <v-col cols="auto" style="padding-left: 0px; padding-top: 4px; padding-right: 12px">
                <v-btn class="mx-2" fab dark small color="primary" style="margin-right: 0px!important"
                       @click="listUsersByUsername">
                    <v-icon dark>mdi-magnify</v-icon>
                </v-btn>
            </v-col>
        </v-row>

        <v-row class="text-center" v-if="filtersOn">
            <v-col class="pa-0" style="margin-bottom: 8px">
                <v-btn rounded color="primary" dark small @click="resetSearchingFilters">
                    <v-icon small left>mdi-close-circle</v-icon>
                    {{removeFiltersText}}
                </v-btn>
            </v-col>
        </v-row>

        <v-row v-if="users.length > 0" class="text-center justify-center">
            <div v-for="user in users"
                 :key="user.id">
                <v-card

                        class="text-center ma-1"
                        style="padding: 0px; width: available; min-width: 105px; max-width: 150px; display: inline-block; margin: auto; margin-bottom: 48px!important">
                    <v-card elevation="0" @click="openUserProfile(user.id)" style="padding: 4px;">
                        <v-img
                                class="white--text align-end"
                                :aspect-ratio="1"
                                :src="user.profileImg"
                        ></v-img>
                        <div>
                            <strong style="font-size: 11px">@{{user.username}}</strong>
                        </div>
                    </v-card>
                    <v-card
                            style="padding: 0px; width: 100%; position: absolute; left: 0px; bottom: -38px;"
                    >
                        <div
                                class="scrolling-wrapper-flexbox text-center justify-center"
                                style=" width: calc(100% - 8px); margin-left: 4px"
                        >
                            <div
                                    v-for="lan in getLanguagesByUserID(user.id)"
                                    :key="lan" class="flag-card ma-1">
                                <v-avatar
                                        style="height: 16px !important; width: 16px !important; min-width: 16px!important;">
                                    <img :src="createPathToFlagImage(lan)" :aspect-ratio="1"
                                         style=" object-position: center; object-fit: cover;">
                                </v-avatar>
                            </div>
                        </div>
                        <!--<div class="text-center justify-center" style="height: 32px; display: flex; width: calc(100% - 8px); padding-left: 6px!important; vertical-align: middle;">
                            <p style="font-size: 16px; color: gray; vertical-align: bottom;display: block; margin: 0px;"><</p>
                            <div
                                    class="scrolling-wrapper-flexbox"
                                    style=" width: calc(100% - 16px)"
                            >
                                <div
                                        v-for="lan in getLanguagesByUserID(user.id)"
                                        :key="lan" class="flag-card ma-1">
                                    <v-avatar
                                            style="height: 16px !important; width: 16px !important; min-width: 16px!important;">
                                        <img :src="createPathToFlagImage(lan)" :aspect-ratio="1"
                                             style=" object-position: center; object-fit: cover;">
                                    </v-avatar>
                                </div>
                            </div>
                            <p style="font-size: 16px; color: gray;">></p>
                        </div>-->
                    </v-card>
                </v-card>
            </div>
            <v-col v-show="users.length == 0">
                <p>{{messageNoUsersFit}}</p>
            </v-col>

        </v-row>
        <v-row v-if="users.length > 0" class="text-center ma-2">
            <v-col>
                <v-btn v-show="!buttonIsDisabled" color="primary" @click="listUsers" :disabled="buttonIsDisabled">
                    {{watchMoreButtonText}}
                </v-btn>
            </v-col>
        </v-row>

        <v-card v-if="users.length == 0 && (filtersOn || usernameFilter != null) && usersListed" class="pa-2 ma-1"
                style="padding-bottom: 24px!important; height: 66vh">
            <v-row class="text-center justify-center">
                <p class="pa-4" style="opacity: 0.6; padding-bottom: 0px!important">{{noUsersText}}</p>
            </v-row>
            <v-row class="text-center justify-center">
                <img :src="sadFaceImg" style="opacity: 0.2; width: 22.5%; max-width: 150px; min-width: 65px">
            </v-row>
        </v-card>


        <v-snackbar
                v-model="snackbar"
                :timeout="4000"
        >
            {{ snackbarText }}
            <v-btn
                    color="blue"
                    text
                    @click="snackbar = false"
            >
                {{closeText}}
            </v-btn>
        </v-snackbar>
    </v-container>
</template>

<script>
    import Constants from "../../store/Constants";
    import {mapActions, mapState, mapGetters, mapMutations} from 'vuex';

    export default {
        name: "SearchHome",
        data() {
            return {
                buttonIsDisabled: true,
                snackbar: false,
                snackbarText: null,
                searchLabel: Constants.TEXT_SEARCH,
                searchByUsernameField: null,
                searchButtonDisabled: true,
                messageNoUsersFit: '',
                watchMoreButtonText: Constants.TEXT_WATCH_MORE,
                rules: {
                    min5Char: value => (value == null || value.length >= Constants.RULE_MIN_VALUE_CHARACTERS_USERNAME || value.length == 0) || Constants.RULE_TEXT_MIN_VALUE_CHARACTERS_USERNAME_TO_START_SEARCHING,
                },
                usersText: Constants.TEXT_USERS,
                toFilterText: Constants.TEXT_TO_FILTER,
                removeFiltersText: Constants.TEXT_REMOVE_FILTERS,
                closeText: Constants.TEXT_CLOSE,
                filtersOn: false,
                noUsersText: Constants.TEXT_NO_USERS_MATCH_SEARCH,
                sadFaceImg: Constants.IMG_EMOJI_SAD_FACE,
                usersListed: false
            }
        },
        methods: {
            ...mapActions('users', ['getSearchedUsers', 'resetSearchedUsers', 'resetFilters']),
            ...mapMutations('users', ['setSearchByUsernameFilter']),
            createPathToFlagImage: function (lang) {
                return "../img/flags/" + lang + ".svg";
            },
            getLanguagesByUserID(userID) {
                return this.$store.getters['users/getLanguagesByUserID'](userID);
            },
            listUsers: function () {
                let self = this;
                self.buttonIsDisabled = true;
                self.usersListed = false;

                let resultGetSearchedUsers = self.getSearchedUsers();

                resultGetSearchedUsers.then((moreUsersToShow) => {
                    self.usersListed = true;
                    self.buttonIsDisabled = !moreUsersToShow;
                })
                    .catch((error) => {
                        self.snackbarText = error;
                        self.snackbar = true;
                    });
            },
            listUsersByUsername: function () {
                let self = this;

                if (self.searchByUsernameField && self.searchByUsernameField.length >= Constants.RULE_MIN_VALUE_CHARACTERS_USERNAME) {
                    self.buttonIsDisabled = true;
                    self.searchButtonDisabled = true;
                    self.usersListed = false;

                    let resultResetSearchedUsers = self.resetSearchedUsers();

                    resultResetSearchedUsers.then(() => {
                        self.setSearchByUsernameFilter(self.searchByUsernameField);

                        self.getSearchedUsers().then((moreUsersToShow) => {
                            self.usersListed = true;
                            self.setSearchByUsernameFilter(null);
                            self.buttonIsDisabled = !moreUsersToShow;
                            self.searchButtonDisabled = false;
                        });
                    })
                        .catch((error) => {
                            self.snackbarText = error;
                            self.snackbar = true;
                        });
                }

            },
            resetSearchingFilters: function () {
                let self = this;
                let resultResetFilters = self.resetFilters();

                resultResetFilters.then(() => {
                    self.filtersOn = self.$store.getters['users/areThereFiltersOn'];
                    self.usersListed = false;

                    self.resetSearchedUsers().then(() => {
                        self.setSearchByUsernameFilter(self.searchByUsernameField);
                        self.getSearchedUsers().then((moreUsersToShow) => {
                            self.usersListed = true;
                            self.buttonIsDisabled = !moreUsersToShow;
                            self.filtersOn = self.$store.getters['users/areThereFiltersOn'];
                        });
                    })
                        .catch((error) => {
                            self.snackbarText = error;
                            self.snackbar = true;
                        });

                })
                    .catch((error) => {
                        self.snackbarText = error;
                        self.snackbar = true;
                    });
            },
            openUserProfile(id) {
                this.$router.push('search/user/' + id);
            },
            openSearchFilter() {
                this.searchByUsernameField = null;
                this.setSearchByUsernameFilter(this.searchByUsernameField);
                this.$router.push('search/filter');
            },
        },
        created() {
            let self = this;
            let resultResetSearchedUsers = this.resetSearchedUsers();

            self.usersListed = false;
            resultResetSearchedUsers.then(() => {
                self.getSearchedUsers().then((moreUsersToShow) => {
                    self.usersListed = true;
                    self.buttonIsDisabled = !moreUsersToShow;
                    self.filtersOn = self.$store.getters['users/areThereFiltersOn'];
                });
            })
                .catch((error) => {
                    self.snackbarText = error;
                    self.snackbar = true;
                });

        },

        computed: {
            ...mapGetters('users', {users: 'getSearchedUsers'}),
            ...mapGetters('users', {usernameFilter: 'getSearchByUsernameFilter'}),
        },
        watch: {

            searchByUsernameField: function (value) {
                if (value == '') {
                    this.searchByUsernameField = null;
                }

                if (value == null) {
                    let self = this;
                    self.setSearchByUsernameFilter(this.searchByUsernameField);
                    let resultResetSearchedUsers = this.resetSearchedUsers();
                    self.usersListed = false;
                    resultResetSearchedUsers.then(() => {
                        self.getSearchedUsers().then((moreUsersToShow) => {
                            self.usersListed = true;
                            self.buttonIsDisabled = !moreUsersToShow;
                            self.filtersOn = self.$store.getters['users/areThereFiltersOn'];
                        });
                    })
                        .catch((error) => {
                            self.snackbarText = error;
                            self.snackbar = true;
                        });
                }
            }
        },
    }
</script>

<style scoped>
    .scrolling-wrapper-flexbox {
        display: flex;
        flex-wrap: nowrap;
        overflow-x: auto;
    }

    .scrolling-wrapper-flexbox::-webkit-scrollbar {
        display: none;
    }

    .flag-card {
        flex: 0 0 auto;
    }

    .bottom-centered-buttons {
        left: 50%;
        transform: translate(-50%, 0);
    }

    .filter-cards-titles {
        padding: 10px;
        padding-bottom: 5px;
        font-size: 18px;
    }

    .v-input__slot {
        margin: 0px;
    }

    p {
        word-wrap: inherit;
    }

    .app-bar-button {
        height: 100% !important;
        min-width: unset !important;
    }

</style>
